const {ccclass, property} = cc._decorator;

@ccclass
export default class PlayButton extends ButtonClickHandler {

    @property(cc.Node)
    container:cc.Node = null;

    @property(Boolean)
    insidePopUp:boolean = false

    loadScene() {
        if(this.insidePopUp) {
            this.container.active=false;
        }
        cc.find("/Canvas").emit("resumeGame",{});
    }

    start () {

    }

    // update (dt) {}
}
